* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'VT323', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
  color: black; 
}


.griditem-container {
  float: left;
  width: 250px;
  height: 250px;
  /* margin-right: 15px; */
  display: block;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  /* margin-bottom: 10px; */
  margin: 20px;
}

@media only screen and (max-device-width : 400px) {
  .griditem-container {
    margin: 1px;
  }
}

.griditem-container span {
  color: #ffffff;
  margin: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  border-radius: 50%;
  color: black;
  width: 22%;
  height: 22%;
}

.griditem-container span:hover {
  border: 2px dotted;
}

.griditem-container>div.griditem {
  float: left;
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.griditem-container>div.griditem .front,
.griditem-container>div.griditem .back {
  float: left;
  display: block;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: -webkit-transform ease 500ms;
  -webkit-transition: -webkit-transform ease 500ms;
  transition: transform ease 500ms;
  transition: transform ease 500ms, -webkit-transform ease 500ms;
}

.griditem-container>div.griditem .front {
  z-index: 2;
  /* background: #19489E; */
}

.griditem-container>div.griditem .back {
  /* background: #9E1919; */
}

.griditem-container .front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

.griditem-container .back {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.griditem-container div.griditem.flip .front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.griditem-container div.griditem.flip .back {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.grid {
  display: inline-block;
  margin: 20px 30px 0px; 
  padding-left: 20px;
  width: 96%;
}

.grid-title{
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card img {
  width: 200px;
  height: 150px;
  margin: 30px 26px;
}

.theme-container {
    padding: 10px 10px 0px;
    font-size: 15px;
}

.theme-option {
    padding: 2px;
}

.theme-option span:hover {
    border-bottom: 2px dotted gray;
}
.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    float: right;
    cursor: pointer;
}


.puppies-background {
    background-color: #77878B
}

.christmas-background {
    background-color: #2f6e4c;
}

.superman-background {
    background-color: #D33E43
}

.disney-background {
    background-color: #304DA5;
}
